.post-content {
  padding: 0 24px;
}

.h2-page {
  margin-left: 13px;
}



.news-list {
  display: flex;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: stretch;

    // flex: 0 0 80%;
    // max-width: 80%;
  }
}


.news-box {
  border: 1px solid #e8e8e8;
  max-width: 330px;
  &:not(last-child) {
    margin: 10px;
  }

}

.news-item {
  display: block;
  padding-bottom: 20px;
  padding: 10px;

  img {
    width: 100%;
    height: 200px


  }
  &__date-text {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;

    span {
      font-size: 12px;
      letter-spacing: 300;
      color: var(--color-link);
      margin-top: 5px;
    }

    svg {
      height: 16px;
      width: 16px;
      margin-right: 4px;
      opacity: .6;
      vertical-align: text-bottom;
      fill: var(--color-link);
    }

  }
  &__title {
    font-size: 16px;
    font-weight: 600;
    color: var(--color-link);
    margin-top: 10px;
  }
}

.right-col {
  flex: 0 0 25%;
  max-width: 25%;
}

.sidebar-posts {
  width: 305px;
  margin-top: 7px;
  &__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  &__img {
    width: 60px;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    margin-left: 10px;
    font-size: 12px;
    font-weight: 400;
    margin-top: -20px;
  }
}

.sidebar-title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin-bottom: 25px;

  a {
    color: var(--color-link);
    font-size: 14px;
    line-height: 15px;
    color: #666;
  }

}

