@media only screen and (max-width: 768px){
  .news-list__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    align-items: stretch;
    }
    .news-box {
      border: 1px solid #e8e8e8;
      max-width: 100%;

    }

    .news-item {
      display: block;
      padding-bottom: 20px;
      padding: 10px;
       img {
        width: 100%;
        height: 100%;
      }
    }

    .right-col {
      display: none;
    }
    .header-top__contacts {
      display: none;
    }


  }

  @media only screen and (max-width: 1400px){


    .right-col {
      display: none;
    }


  }



