.data-popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1001;
  pointer-events: none;
  display: none;
  animation: var(--fade);
  .wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;

  }
  .data-content {
    position: relative;
    background-color: var(--white-color);
    padding: 40px 20px;
    width: 400px;
    max-width: calc(100vw - 50px);
    max-height: calc(100vh - 60px);
    border-radius: 5px;
    overflow-y: auto;
    pointer-events: auto;
  }
  .close-trigger {
    position: absolute;
    top: 0;
    right: 0;
    height: 40px;
    display: flex;
    align-items: center;
  }
  .form {
    position: relative;
    span {
      position: absolute;
      top: 0;
      right: 0;
      height: 40px;
      display: flex;
      align-items: center;
      &:hover {
        color: var(--grey-color);
      }
    }
    // span {
    //   top: auto;
    //   bottom: 0;
    //   right: 10px;
    //   cursor: pointer;
    //   transition: var(--transition-color);

    // }

  }
  .form input {
    color: var(--dark-color);
    background-color: var(--light-color);
    border: 0;
    padding: 7px 34px 7px 10px;
    border-radius: 3px;
    font-size: var(--fs14);
  }
  label {
    font-weight: 600;
    font-size: var(--fs14);
    margin-bottom: 10px;
    display: flex;
  }
  .media-share {
    margin-top: 20px;
    ul {
      display: flex;
      gap: 20px;
    }
  }
  form > div {
    margin-top: 20px;
  }
  form .button {

      button {
      cursor: pointer;
    }
  }
  h3, form .button {
    text-align: center;
  }
  input, textarea {
    color: var(--dark-color);

    border: 1px solid var(--dark-color);
    padding: 7px 34px 7px 10px;
    border-radius: 3px;
    font-size: var(--fs14);
  }

}

.d-review {
  .data-content {
    --data-width: 500px;
  }
  .dotgrid .wrapper {
    --gutter: 10px;
  }
  .stars input {
    display: none;
  }
  .stars label {
    font-size: 16px;
    margin: 0;
    cursor: pointer;
  }
  .stars > input:checked ~ label {
    color: #ffa502;
  }
  :where(.rating, .stars) {
    display: flex;
    gap: 7px;
  }
  .stars {
    flex-direction: row-reverse;
  }
}

.data-popup.active {
  display: block;
}


