
.inner-header  {
  line-height: 80px;


  :where(.wrap, .menu > ul),
  .list-inline>ul {
    display: flex;
    align-items: center;
  }

  :where(.header-left, .header-right) {
    li {
      display: flex;
      align-items: center;
      height: 80px;
    }
  }

  // Serach

  .search-float {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: auto;
    height: 80px;
    background-color: var(--white-color);
    z-index: 1000;
    display: none;


    .search {
      position: relative;
      :where(i, .submit) {
        position: absolute;
        left: 0;
        top: 0;
      }

      .input {
        outline: 0;
        padding: 15px 50px;

        display: flex;
        border: 0;
        height: 80px;
        font-size: 18px;
      }

      i {
        font-size: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 100%;
        cursor: pointer;
      }

      :where(.submit, i:last-child) {
        left: auto;
        right: 0;
      }

    }
  }

  .search-float.active {
    display: block;
  }

  // Mobile-menu


  .mobile-menu {
    z-index: 1001;
    pointer-events: none;

    .wrap {
      position: relative;
      max-width: calc(100% - 40px);
      width: 360px;
      height: 100%;
      background-color: var(--white-color);
      pointer-events: auto;
    }
  }






  .list-inline  {

    a {
      position: relative;
      font-size: 24px;
      padding: 0 10px;
      line-height: 1;
      display: flex;
      transition: var(--transition-color);

      &:hover {
        color: var(--light-color)
      }
    }



  }

  .header-left  {
    .list-inline {
      display: none;

      @media (min-width: 992px) {
        display: block;
      }
    }

  }


  .header-left, .header-right {
    flex-grow: 1;
  }

  .header-right {
    ul {
      justify-content: flex-end;
    }
  }

  .header-center {

    .mega-content {
      display: grid;
      grid-template-columns: 2fr 1fr;
      gap: 60px;

      .links {
        display: grid;
        grid-template-columns:  repeat(auto-fill, minmax(135px, 1fr));
        gap: 15px;

        .dot-title {
          font-size: 18px;
          margin-bottom: 10px;
        }
      }

      @media (min-width:1400px) {
        .mega-content {
          grid-template-columns: 1fr 1fr;
        }
      }
    }

    nav {
      display: none;
      grid-template-columns: 1fr 1fr;
      gap: 180px;
      max-width: 720px;

      @media (min-width: 992px) {
        display: grid;
      }

    }
    .menu>ul>li>a {
      font-size: var(--fs14);
      font-weight: 600;
      text-transform: uppercase;
      padding: 0 15px;
      display: flex;
      gap: 5px;

    }

    .menu {
      .sub-menu>li {
        padding: 5px 30px;
      }

      li {
        &:hover {
          :where(.sub-menu, .sub-mega) {
            display: block;
          }
        }
      }


      @media (min-width: 992px) {
        .sub-mega {
          left: 0;
          right: 0;
          padding: 50px 0;
          // display: block;

        }


        :where(.sub-menu, .sub-mega) {
          position: absolute;
          top: auto;
          line-height: initial;
          background-color: var(--white-color);
          box-shadow: var(--shadow);
          z-index: 1000;
          padding: 10px 0;
          display: none;
          animation: var(--fade);


          li {
            padding: 5px 0px;

          }

        @keyframes fade-bottom {
          0% {
            transform: translateY(10px);
            opacity: 0;
          }
          100% {
            transform: translateY(0);
            opacity: 1;
          }

        }

      }

      }

      .list-block {
        a {
          position: relative;
          font-size: var(--fs14);
          color: var(--grey-color);
          padding: 3px 0;
          display: inline-block;



          &::before {
            content: '';
            position: absolute;
            width: 0px;
            height: 0px;
            background-color: var(--dark-color);
            left: -20px;
            top: 50%;
            border-radius: 50%;
            transform: translateY(-50%);
            transition: width .3s, height .3s;
          }

          &:hover {
            color: var(--dark-color);
            &::before {
              width: 8px;
              height: 8px;
            }

          }
        }

      }




    }


    .branding {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      pointer-events: none;

      a {
        font-family: var(--second-font);
        font-size: 30px;
        line-height: inherit;
        pointer-events: auto;

      }
    }


  }


}

.overlay,.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.overlay {
  background-color: var(--dark-color);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 999;
  transition: opacity .3s, visibility .3s;
}

.overlay.active {
  opacity: .75;
  visibility: visible;
  pointer-events: all;
}

// scroll-style


.item-floating {
  position: absolute;
  font-size: 10px;
  font-weight: 600;
  top: -8px;
  right: 0;
  background-color: var(--primary-color);
  color: var(--white-color);
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.item-floating, .header-center, .branding {
  display: flex;
  align-items: center;
  justify-content: center;

}


.menu-trigger, .close-trigger {
  position: relative;
  font-size: 24px;
  padding: 0 10px;
  line-height: 1;
  display: flex;
  transition: var(--transition-color);
  a {
    &:hover {
      color: var(--light-color);
    }
  }


  svg {
    width: 22px;
    height: 22px;
  }



  @media (min-width: 992px) {
    display: none;
  }
}





.dotgrid {

  .actions {

    ul {
      display: flex;
      justify-content: center;
      gap: 10px;
      li {
          a{
            font-size: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 42px;
            height: 42px;
            background-color: var(--white-color);
            // background-color: var(--light-color);
            border-radius: 50%;
            transition: var(--transition-background), var(--transition-color);

            &:hover {
              background-color: var(--dark-color);
              color: var(--white-color);
            }
          }
      }
    }
  }

  .wrapper {
    display: var(--display, grid);
    --grid-col: 175px;
    grid-template-columns: repeat(auto-fit, minmax(max(var(--grid-col), 100%), 1fr));
    gap: var(--gutter, 30px);
    // grid-template-columns: unset;
    // gap: unset;


  }


  .dot-image {
    position: relative;
    margin-bottom: 15px;
    overflow: hidden;

    &:hover {
      .thumbnail, .hover, .actions {
        opacity: 1;
        transform: scale3d(1.1, 1.1, 1.1) translateZ(0);
      }
    }



    .thumbnail, .hover, .actions, .label {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

    }

    .actions{
      z-index: 2;
      top: auto;
      bottom: 40px;
      transform: translate3d(0, 100%, 0);
      opacity: 0;
      transition: all .3s cubic-bezier(0,0,.2, 1);
    }

  }

  .dot-info {
    text-align: center;

    .dot-title {
      font-family: var(--primary-font);
      font-size: (var(--fs14));
      font-weight: 600;
      line-height: 34px;
      a{
        transition: var(--transition-color);
        &:hover {
          color: var(--light-color);
        }
      }
    }

    .product-price {
      .before {
        color: var(--light-color);
        text-decoration: line-through;
      }
    }
  }

}


.dotgrid .thumbnail.hover  {
  opacity: 0;
  transition: opasity .75s ease, transform 1.1s cubic-bezier(.15, .75, .5, 1) 0s;
  backface-visibility: hidden;
  transform: translateZ(0);

}

.dotgrid .dot-image .label {
  top: auto;
  bottom: 20px;
  display: flex;
  justify-content: center;
  gap: 10px;

  span {
    font-size: 12px;
    font-weight: 600;
    background-color: var(--primary-color);
    color: var(--white-color);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 42px;
    height: 42px;

    border-radius: 50%;
    transition: var(--transition-background), var(--transition-color);

  }

}


@media (min-width: 1200px) {
  .container {
    .wide {
      max-width: 1720px;
      padding-bottom: 40px;
    }
  }

}



// Mobile menu
.mobile-menu {
  z-index: 1001;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;




  nav {
    .sub-menu {
      padding-left: 25px;
      max-height: 0;
      overflow: hidden;
      visibility: hidden;
      transition: .3s ease-in-out;
    }

    li.active .sub-menu {
      max-height:100% ;
      visibility: visible;
    }




  }

  .wrap {
    position: relative;
    max-width: calc(100% - 40px);
    width: 360px;
    height: 100%;
    background-color: var(--white-color);
    pointer-events: auto;
    // transform: translateX((var(--transX, -100%)));
    // transition: var(--transition-transform);
    transform: translateX( 0%, -100%);
    transition: var(--transition-transform);
    .active {
      transform: translateX(0);
    }
  }

  .close-trigger, .child-trigger  {
    position: absolute;
    top: 0;
    right: -40px;
    width: 40px;
    height: 40px;
    color: var(--white-color);
    display: flex;
    align-items: center;
    justify-content: center;
  }


  .main-menu {
    height: 100%;
    padding: 0 20px;
    display: flex;
    flex-direction: column;

  }


  .child-trigger {
    color: var(--dark-color);
    font-size: 20px;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 50%;
    transition: var(--transition-background);
    z-index: 1;
    &:hover {
      background-color: var(--light-color);
    }

  }

}

.mobile-menu.active {
  visibility: visible;
  opacity: 1;
}


.mobile-menu nav>ul {
    padding: 20px 0;
  }

  .mobile-menu nav > ul > li > a {
    position: relative;
    padding: 12px 0;
    font-weight: 600;
    transition: var(--transition-color);

  }
  .mobile-menu nav>ul>li :where( li, a){
    display: block;
  }


  .mobile-menu nav>ul>li> a:hover {
    color: var(--grey-color);
  }






  .list-block {
    a {
      position: relative;
      font-size: var(--fs14);
      color: var(--grey-color);
      padding: 3px 0;
      display: inline-block;



      &::before {
        content: '';
        position: absolute;
        width: 0px;
        height: 0px;
        background-color: var(--dark-color);
        left: -20px;
        top: 50%;
        border-radius: 50%;
        transform: translateY(-50%);
        transition: width .3s, height .3s;
      }

      &:hover {
        color: var(--dark-color);
        &::before {
          width: 8px;
          height: 8px;
        }

      }
    }

  }










