// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --primary-font: "Muller", sans-serif;
  --second-font: "MullerBold", sans-serif;

  --mx-width: 1200px;
  --fs14: 14px;

  //color
  --primary-color: #2bcbba;
  --light-color: #d1d8e0;
  --grey-color: #687080;
  --red-color: #ff4757;
  --flashwhite-color: #f1f2f6;
  --white-color: #ffffff;
  --dark-color: #212121;
  --color-azet: #7c2e3b;
  --stars-color: #ffa502;

  --transition-color: color .3s;
  --transition-background: background-color .3s;
  --transition-border: border .3s;
  --transition-transform: transform .3s;

  --shadow: 0px 10px 20px 0px rgb(0 0 0 /20%);

  --fade: fade-bottom .3s cubic-bezier(0.39, 0.575, .0565, 1) both;

  --display: flex;
  --grid-col: grid;
  --gutter: 60px;


}


