.inner-footer {
  background-color: var(--flashwhite-color);
  line-height: 1.4;
  padding-bottom: 30px;
  p {
    font-size: var(--fs14);
  }

  .wrap {
    .dot-title {
      margin-bottom: 10px;
    }
    .top {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 60px;
    }
    .bottom {
      padding-top: 40px;
      padding-bottom: 40p;
      display: flex;
      justify-content: space-between;
      .list-inline > ul {
        display: flex;

      }
    }

    @media (min-width:992px) {
      .wrap >div {
        flex: nowrap;
      }
    }


  }
  .subscribe {
    width: 100%;
    h3 {
      margin-bottom: 20px;

    }
  }

  .list-inline  {

    a {
      position: relative;
      font-size: 24px;
      padding: 0 10px;
      line-height: 1;
      display: flex;
      transition: var(--transition-color);

      &:hover {
        color: var(--light-color)
      }
    }



  }

  .search {
    max-width: 400px;
    position: relative;
    :where(i, .submit) {
      position: absolute;
      left: 0;
      top: 0;
    }


    .input {
      outline: 0;
      padding: 15px 50px;
      display: flex;
      border: 1;
      height: 60px;
      font-size: 18px;
    }

    i {
      font-size: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50px;
      height: 100%;
      cursor: pointer;
    }

    :where(.submit, i:last-child) {
      left: auto;
      right: 0;
    }
    .submit {
      border: 0;
      background-color: transparent;
      color: transparent;
      width: 50px;
      height: 100%;
      right: 0;
      z-index: 1;
      cursor: pointer;
    }

    :where(input, textera, select) {
      font-size: var(--fs14);
      border: 1px solid var(--light-color);
      outline: 0;
      background-color: transparent;
      padding: 10px 20px;
      transition: border .3s;
      &:focus {
        border-color: var(--dark-color);
      }
      &::placeholder {
        color: var(--grey-color);
      }
    }

  }
}


