.slider :where(.image, ob-cover) {
  position: relative;
  .ob-cover {
    height: 430px;

    img {
      position: absolute;
      // height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  .title {
    font-size: clamp(30px, -0.875em + 8.333vw, var(--fs-max, 40px));
  }




  .title-info {
    background-color: var(--white-color);

    .wrap {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 30px;
      padding-bottom: 5vw;
      max-width: 600px;
      margin: 0 auto;
      text-align: center;
      .price{
        position: relative;
        font-size: 30px;
        width:  80px;
        height: 80px;
        margin-top: -40px;
        background-color:  var(--dark-color);
        color: var(--white-color);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
      }



    }


  }




}

.slider {
  .costum-pagination {
    position: absolute;
    right: 30px;
    top: 50%;
    bottom: 50%;
    .swiper-pagination{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .swiper-pagination-bullet {
        position: relative;
        width: 24px;
        height: 24px;
        opacity: 1;
        background-color: transparent;
        &::before {
          content: '';
          position: absolute;
          width: 8px;
          height: 8px;
          top: 50%;
          left: 50%;
          background-color: var(--dark-color);
          border: 1px solid var(--dark-color);
          border-radius: 50%;
          transform: translate(-50%, -50%);
          transition: width .2s, height .2s, transform .2s;
        }
      }
      .swiper-pagination-bullet-active {
        &::before {
          background-color: transparent;
          width: 15px;
          height: 15px;
        }
      }
    }
  }

  .title-info :where(span, h3, .button) {
    transform: translateY(30px);
    opacity: 0;
    visibility: hidden;
    transition: transform .75s, opasity .75s, visibility .75s;
  }
  .swiper-slide-active .title-info :where(span, h3, .button) {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;

  }
  .swiper-slide-active .title-info  {
    transition-delay:  .5s;
    .button {
      transition-delay: .75s;
    }
  }

}


@media (min-width: 992px) {
    .slider {



    .ob-cover {
      // height: 430px;
      height: calc(100vh - 80px);
      img {
        position: absolute;
        height: 100%;
        width: 100%;
        object-fit: cover;
      }

    }

    .title-info {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: transparent;
      align-items: center;


      h3 {
        --fs-max: 60px;
        color: var(--white-color);
      }

      .button {
        a {
          border-color: var(--white-color);
          color: var(--white-color);
          &:hover {
            border-color: var(--dark-color);
          }
        }
      }

      // .wrap {
      //   margin-bottom: 200px;
      // }



    }
  }

}


