.breadcrumb {
  width: 100%;
  line-height: initial;
  padding: 30px 0 50px;
  ul {
    justify-content: center;
    &:where(li, li a) {
      font-size: var(---fs14);
      padding: 0;
    }
    li {
      position: relative;
      margin-right: 10px;
      &:not(:first-child) {
        padding-left: 25px;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          width: 16px;
          height: 16px;
          background-image: url('data:image/svg+xml;base64,');
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
      &:last-child {
        color: var(--grey-color);
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
}
