.product {
  .wrapper {
    --grid-col: 331px;


  }

  .scrollto > .wrapper {
    display: flex;
    padding-bottom: 5px;
    overflow: auto;
    overscroll-behavior-inline: contain;
    scroll-snap-type: inline mandatory;
  }


  .main-image {
    min-width: 331px;
    img {
      width: 100%;

    }
  }
  .outer-thumb {
    display: none;
    .item {
      height: fit-content!important;
      cursor: pointer;
      &:hover > div {
        border: 1px solid var(--dark-color);
        img {
          transform: scale3d(.85, .85, 85);
        }
      }
      }
      img {
        transform: var(--transition-transform);
      }

    }
    .swiper-slide-thumb-active > div {
      border: 1px solid var(--dark-color);


    }
    .swiper-slide-thumb-active {
      // border: 1px solid var(--dark-color);
      img {
        transform: scale3d(.85, .85, 85);
      }

  }
  .thumb-wrap {
    position: relative;
    width: 70px;
    height: 90px;
  }
  .custom-pagination {
    position: relative;
    height: 50px;
  }


  .detail {
    padding-top: 30px;
    margin: 70px 0;
    .dotgrid {
      .wrapper{
        --grid-col: 250px;
        --gutter: 30px;
      }
    }
  }
  nav {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 76px;
      height: 2px;
      width: 100%;
      background-color: var(--light-color);
    }

  }
  nav {
    .item-floating {
      right: -18px;
      top: 0;
      background-color: var(--dark-color);
    }
    ul {

      li {

        a {
          position: relative;
          display: block;
          font-size: 28px;
          padding: 20px 0;
          margin-right: 30px;
          white-space: nowrap;
          color: var(--grey-color);
          &::before {
            content: '';
            position: absolute;
            height: 2px;
            width: 0;
            left: 0;
            bottom: 12px;
            background-color: var(--dark-color);
            transition: width .3s;
          }
        }

      }

    }
    ul :is(li.active a, li a:hover) {
      color: var(--dark-color);
    }
  }
  nav ul li.active {
    a{
      &::before{
        width: 100%;
      }
    }
  }
  .product-about {
    display: none;
    animation: var(--fade);

    :where(h3, h4) {
      font-family: var(--second-font);
      margin-bottom: 15px;
      padding-top: 15px;

    }
    h3 {
      font-size: 22px;
    }
    h4 {
      font-weight: 600;
    }
    ul {
      margin-left: 20px;
      list-style-type: disc;
    }
    .list-block.list-text {
      min-width: 320px;
    }

    .text-block {
      padding-bottom: 30px;
    }

  }
  .product-about.custom {
    h3 {
      font-size: 50px;
      font-weight: 600;
      text-align: center;
      color: var(--light-color);
    }

  }
  .product-about .wrapper {
    display: flex;
    flex-direction: column;
  }
  .product-about.active {
    display: block;
  }

  .review {
    max-width: 770px;

    .head-review {
      display: flex;
      align-items: center;
      justify-content: space-between;
      // margin-bottom: 30px;
      border-bottom: 1px solid var(--light-color);
      strong {
        font-size: 50px;
      }
      span {
        position: relative;
        padding-left: 20px;
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 12px;
          height: 100%;
          width: 1px;
          background-color: var(--grey-color);
          transform: skewX(-25deg);
        }
      }
      .primary-btn {
        border-width: 1px;
        text-transform: unset;
        font-weight: 500;
      }
    }
    .comment-block {
      padding-bottom: 40px;
    }
    .profile {
      display: flex;
      align-items: center;
      margin-bottom: 20px;


      .profile-img {
        width: 72px;
        height: 72px;
        border-radius: 50%;
        margin-right: 20px;
        overflow: hidden;
      }
      .grouping {
        line-height: initial;
      }
      .name {
        font-weight: 600;
        margin-bottom: 5px;
      }
      .date {
        font-size: 12px;
        margin-top: 8px;
      }


    }
  }






}
.product .thumb-name {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

}
.page-single {
  .carousel {
    padding-top: 40px;
    padding-bottom: 40px;

  }
}





.dotgrid {
  .wrapper {
    display: grid;
  }

}

.summary {
  .wrap:has(button) {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  .wrap > button, .qty button {
    width: 40px;
    height: 40px;
    transition: box-shadow .3s,
                var(--transition-border),
                var(--transition-background),
                var(--transition-color);
  }
  .qty {
    display: flex;
    font-size: 18px;
    min-width: 110px;
    height: 50px;
    margin: 0 20px 20px 0;
    background-color: var(--flashwhite-color);
    input {
      text-align: center;
      border: 0;
      outline: 0;
      background-color: transparent;
    }
    button {
      font-size: 22px;
      height: 100%;
      background-color: transparent;
      &:hover {
        color: var(--grey-color);
      }
    }
  }


  button {
    border-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    cursor: pointer;
  }
  .product-stock .wrap{
    display: flex;
    gap: 5px;
    i {
      color: var(--primary-color);
      padding-left: 10px;
    }
  }
  .product-color {
    button.tosca {
      background-color: #a9cdcc;
    }
    button.brown {
      background-color: #966548;
    }
    button.ocean {
      background-color: #81b8cc;
    }
    button.selected, button:hover:not(.selected) {
      box-shadow: inset 0 0 0 4px var(--white-color);

    }
    button {
      border-radius: 50%;


    }
  }
  .product-action :where(.addcart, .buynow) button {
    width: 100%;
    height: 50px;
    border-color: var(--dark-color);
    padding: 0;
  }
  .product-action {
    display: grid;
    grid-template-columns: 1fr 2fr;
    .buynow {
      grid-column-start: 1;
      grid-column-end: 3;
    }
    .addcart button {
      background-color: var(--white-color);
      &:hover {
        background-color: var(--dark-color);
      }
    }


  }
  .product-control {


    ul {
      display: flex;
      align-items: flex-start;
      flex-flow: wrap;
      gap: 20px;

      li:first-child a:hover {
        color: var(--red-color);
      }

    }
  }
  .product-control a {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 0;
    gap: 5px;
    i {
      color: var(--darck-color);
      padding-left: 10px;

    }
    span {
      // font-size: var(--fs14);
      font-size: 18px;
    }
  }
  .product-size {
    button {
      font-size: var(--fs14);
      line-height: 44px;
      border: 1px solid var(--light-color);
      border-radius: 5px;
      background-color: transparent;
      &:hover:not(.selected, [disabled]) {
        border-color: var(--dark-color);
      }
      &:disabled {
        pointer-events: none;
      }
    }
    button.selected {
      background-color: var(--dark-color);
      color: var(--white-color);
      border-color: var(--dark-color);
    }
  }
  .addcart {
    text-align: center;
  }




  .entry {
    display: flex;
    flex-direction: column;
    gap: 30px;
    h1 {
      margin: 0;
    }
  }

  .product-group {
    display: flex;
    justify-content: space-between;
  }
  .product-price {
    display: flex;
    align-items: center;
    .current {
      font-size: 30px;
    }
    .wrap {
      position: relative;
      display: flex;
      flex-direction: column;
      padding-left: 30px;


      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 12px;
        height: 100%;
        width: 1px;
        background-color: var(--grey-color);
        transform: skewX(-25deg);
      }

    }
    .discount {
      display: inline-block;
      width: 44px;
      font-size: var(--fs14);
      padding: 5px;
      background-color: var(--primary-color);
      color: var(--white-color);
      margin-left: -10px;
      text-align: center;
      border-radius: 5px;
      line-height: 1;
    }
    .before {
      color: var(--light-color);
      text-decoration: line-through;
    }
  }
  .product-rating {
    line-height: initial;
    text-align: right;
    a {
      font-size: var(--fs14);
      padding-left: 10px;
      color: var(--grey-color);
      text-decoration: dotted;
      text-decoration-line: underline;
      white-space: nowrap;
      transition: var(--transition-color);
      &:hover {
        color: var(--dark-color);
      }
    }
  }
  .shipping {
    padding-top: 30px;
    border-top: 1px solid var(--flashwhite-color);
    li {
      position: relative;
      padding-left: 35px;
      margin-top: 10px;
      line-height: 1.5;
       i {
        position: absolute;
        top: 0;
        left: 0;
        font-size: 24px;
        line-height: 1;
      }
      .grey-orders {
        color:var(--light-color) ;
      }
    }
  }

  // @media (max-width:768px) {
  //   .dotgrid .wrapper  {
  //     display: flex;
  //     flex-direction: column;

  //   }
  //   .dotgrid .image {
  //     max-width: 100%;
  //     margin: 0;
  //     position: relative;
  //     top: 30px;
  //   }


  // }



}







@media (min-width: 481px) {


  .product.dotgrid .wrapper {
    gap: var(--gutter, 30px)
  }

  .product.dotgrid .image {
    position: relative;
    min-width: 400px;
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
    height: min-content;
    overflow: hidden;
  }
  .product .outer-main {
    width: calc(100% - 85px);
    min-width: 300px;
    height: 100%;
    margin: 0 0 0 85px;

  }
  .product .outer-thumb {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
  .custom-pagination {
    display: none;
  }
  .dotgrid .wrapper {
    display: flex;
  }

  .comment {
    padding-left: 92px;
  }





}

@media (min-width:768px) {
  .product.dotgrid .image {
    max-width: 100%;
    margin: 0;
    position: sticky;
    top: 30px;
  }
  .product nav ul {
    justify-content: center;
  }

}

@media (min-width: 1400px) {


  .product {

    .outer-main {
      width: 100%;
      margin: 0;
    }
    .image {
      .outer-thumb {
        position: absolute;
        top: 0;
        left: -100px;
        height: 100%;
      }
    }


  }
  .product.dotgrid {
    .image {
      overflow: unset;
    }

  }

}


