.page-home main > div:not(.slider, .frominsta), .inner-footer {
  padding: 100px 0;
}



.guide {
   background-color: var(--flashwhite-color);
   .dot-grid {
      .wrapper {
        --grid-col: 260px;
      }
   }

   .dot-image {
    .thumbnail {
      img {
        width: 100%;
      }
    }
   }


   .dot-title {
    font-size: 14px;
    font-weight: 400;
    padding:  10px 0;
   }
   .dot-info {
    p {
      line-height: 1.4;
    }
   }

   .wrapper {
    .item {
      width: 290px;
      scroll-snap-align: start;
      flex-shrink: 0;
    }
   }


   @media (min-width:992px) {
    .wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      // display: flex;
      // padding: 0;
      // overflow: unset;
      // scroll-snap-type: unset;
      // overscroll-behavior-inline: unset;


    }
}
}
main > div .heading {
  text-align: center;
  margin-bottom: 60px;
}

:is(.guide, .fromblog, .frominsta) .dot-image .thumbnail {
  position: relative;
  opacity: unset;
}

:is(.guide, .fromblog) .dot-image .thumbnail  img {
  width: 100%;
}


.fromblog {
  .dot-title {
    --primary-font: var(--second-font);
    font-size: 18px;
    font-weight: 400;
    margin-top: 15px;
  }
  .dot-info > a {
    display: inline-block;
    font-size: 12px;
    text-transform: uppercase;
    border: 1px solid var(--light-color);
    color: var(--grey-color);
    padding: 1px 10px;
    transition: var(--transition-background),
                var(--transition-color),
                var(--transition-border);

    &:hover {
      background-color: var(--dark-color);
      color: var(--white-color);
      border-color: var(--dark-color);
    }
  }

  .wrapper {
    .item {
      width: 290px;
      scroll-snap-align: start;
      flex-shrink: 0;
    }
   }

   .scrollto {
    .wrapper {
      .item {
        width: unset;
      }
    }
   }

   .scrollto .wrapper::-webkit-scrollbar-thumb {
    --border-color: var(--lite-color);
   }
   .scrollto .wrapper:hover::-webkit-scrollbar-thumb {
    --border-color: var(--grey-color);
   }

   .button {
    text-align: center;
    margin-top: 40px;
   }

}

.frominsta  {
  img {
    width: 100%;
  }
  .dot-image {
    margin: 0;
  }

  .dotgrid {
    // background-color: var(--flashwhite-color);
    .wrapper {
      display: var(--display, grid);
      gap: var(--gutter, 0);

      .item {
        width: 234px;
        scroll-snap-align: start;
        flex-shrink: 0;
      }
    }
  }
  .actions {
    font-size: 20px;
    width: 50px;
    height: 50px;
    background-color: var(--white-color);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin: 0 auto;
    pointer-events: none;
  }
  .scrollto .wrapper::-webkit-scrollbar-thumb {

    background-color: white;
   }
   .scrollto .wrapper:hover::-webkit-scrollbar-thumb {
    // --border-color: var(--flashwhite-color);
    background-color: var(--grey-color);
   }

}


.carousel {
  .nav .swiper-button-disabled {
    opacity: 0;
  }
  .nav > div {
    width: 40px;
    height: 40px;
    border: 1px solid var(--grey-color);
    border-radius: 50%;
    color: var(--dark-color);
    font-size: 20px;
    transition: var(--transition-background),
                var(--transition-color),
                var(--transition-border);

    &::after {
      content: none;
    }
    &:hover {
      color: var(--white-color);
      background-color: var(--dark-color);
      border-color: var(--dark-color);
    }
  }

  .inner-wrapper {
    position: relative;
    text-align: center;
  }
   .dotgrid {
      .wrapper {
        display: flex;
        grid-template-columns: unset;
        gap: unset;
      }
  }


}

@media (min-width: 1400px) {
  .carousel {

    .inner-wrapper {
      max-width: 1250px;
      margin: 0 auto;
      padding: 0 40px;
    }

    // .nav {
    //   .swiper-button-next {
    //     right: -40px;
    //   }
    //   .swiper-button-prev {
    //     left: -40px;
    //   }
    // }
  }



}
@media (min-width: 480px) {
  .carousel {
    .dotgrid {
      .item {
        width: 270px;
      }
    }
  }

}
