

html {
  scroll-behavior: smooth;
  // overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}


* {
  margin: 0;
  padding: 0;
}


body {
  font-family: var(--primary-font);
  font-size: 16px;
  font-weight: 400px;
  line-height: 1.8;
  color: var(--dark-color);
  background-color: var(--white-color);

}

a {
   text-decoration: none;
   color: inherit;
   -webkit-tap-highlight-color: transparent;
}

img {
  max-width: 100%;
  vertical-align:  middle;

}

ul {list-style: none;}

p {
  margin: 0 0 15px;
}

input, textarea, select {font: inherit; width: 100%;}

input[type=checkbox], input[type=radio] {width: auto;}

input::placeholder, textarea::placeholder {color: inherit;}

button {font: inherit;}

strong { font-weight: 600;}

h1,h2,h3,h4 {
  font-family: var(--second-font);
  font-weight: 400;
  line-height: normal;
}
h1 {
  font-size: 1.75em;
}
h2 {
  font-size: 2.5em;
}
h3 {
  font-size: 2em;
}
h4 {
  font-size: 1em;
}

.container {
  max-width: var(--mx-width, 1200px);
  padding: 0 15px;
  margin: 0 auto;
}

.grey-color {
  color: var(--grey-color);
}


// Scroll Style

.scrollto > .wrapper {
  --display: flex;
  padding-bottom: 20px;
  overflow: auto;
  overscroll-behavior-inline: contain;
  scroll-snap-type: inline mandatory;
}

.scrollto .wrapper::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.scrollto .wrapper::-webkit-scrollbar-thumb {
  --border-color: var(var(--white-color));
  background-color: var(--light-color);
  border: 3px solid var(--border-color);
  border-radius: 20px;
}

.scrollto .wrapper:hover::-webkit-scrollbar-thumb {
  background-color: var(--grey-color);
}

.scrollto .wrapper::-webkit-scrollbar-thumb:hover {
  background-color: var(--grey-color);
}


.button {

  // margin-top: auto;
  // padding: 20px 0 40px;
  // text-align: center;

  [class*=-btn] {
    font-size: var(--fs14);
    font-weight: 600;
    text-transform: uppercase;
    padding: 0 30px;
    display: inline-block;
    line-height: 42px;
    border-width: 2px;
    border-style: solid;
    transition: var(--transition-background),
                var(--transition-color),
                var(--transition-border);
  }
}

.primary-btn {
  border-color: var(--dark-color);
}

.primary-btn:hover {
  background-color: var(--dark-color);
  color: var(--white-color);
}

.secondary-btn:hover {
  background-color: transparent;
  border-color: var(--dark-color);
  color: var(--dark-color);
}

.mobile-menu {

  .button {
    margin-top: auto;
    padding: 20px 0 40px;
   a {
    text-align: center;
    display: block;
    margin-top: 10px;
  }
}

}
.secondary-btn {
  background-color: var(--dark-color);
  color: var(--white-color);
}





